import {
    Button,
    FormControl,
    Grid, IconButton,
    Link,
    List,
    ListItem, ListItemSecondaryAction,
    ListItemText,
    Paper,
    TextField,
    Typography
} from "@mui/material";
import {useEffect, useState} from "react";
import {NetworkButton} from "../components/Coineus";
import {CHAIN_IDS} from "../constants";
import {useSelector} from "react-redux";
import launchpad_abi from "../abis/Coineus/Launchpad/CoineusLaunchPad.json";
import Web3 from "web3";
import CoineusLoading from "../components/CoineusLoading";
import {toWei} from "../utils/format";
import {makeBatchRequest} from "../utils/promisify";
import SettingsIcon from '@mui/icons-material/Settings';
import {Link as RouterLink} from "react-router-dom";

const LAUNCHPAD_CONTRACT = "0x7b5A33F79036a21c0f3883D00186043fa4baE2FB";


export default function Launchpad() {

    const {wallet, rpc} = useSelector(state => state.coineus);
    const [pending, setPending] = useState(false);

    const [name, setName] = useState('');
    const [symbol, setSymbol] = useState('');
    const [totalSupply, setTotalSupply] = useState('');
    const [taxAmount, setTaxAmount] = useState('');

    const [created, setCreated] = useState(null);
    const [launchFee, setLaunchFee] = useState(0);

    const [tokens, setTokens] = useState([]);
    const [tokensByDeployer, setTokensByDeployer] = useState([]);

    const getInfo = () => {

        if(!wallet.address)return;

        const web3 = new Web3(rpc.fuse);
        const launchpad = new web3.eth.Contract(launchpad_abi, LAUNCHPAD_CONTRACT);

        const calls = [
            launchpad.methods.fee().call,
            launchpad.methods.getAllTokens().call,
            launchpad.methods.getTokensByDeployer(wallet.address).call
        ]

        makeBatchRequest(web3, calls).then(resp => {

            const [
                fee,
                _tokens,
                _tokensByDeployer
            ] = resp;

            setLaunchFee(fee);
            setTokens(_tokens);
            setTokensByDeployer(_tokensByDeployer);
        });
    }

    useEffect(getInfo, [wallet.address]);


    const launch = () => {

        const web3 = new Web3(window.provider);
        const launchpad = new web3.eth.Contract(launchpad_abi, LAUNCHPAD_CONTRACT);

        launchpad.methods.LaunchToken(name, symbol, totalSupply, taxAmount)
            .send({from: wallet.address, value: toWei(launchFee)})
            .once('transactionHash', function (tx) {
                setPending(true);
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {

                const {
                    contract_address,
                    taxCollector
                } = receipt.events.NewToken.returnValues;

                setCreated({contract_address, taxCollector});
                setPending(false);
                getInfo();
            });
    }

    return <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>

        <Grid item xs={12}>
            <Paper style={{padding: 20, border: '1px solid #fbca00'}}>
                <Typography color="primary" fontWeight={700}>Launchpad Beta</Typography>
                <Typography>Coineus Launchpad is currently in testing. It is not meant for deploying your final token at this moment. While testing do not use your eventual token name/symbol.</Typography>
            </Paper>
        </Grid>

        <Grid item xs={12}>
            <Paper style={{padding: 20}}>
                <Grid container spacing={2}>

                    <Grid item xs={12}>
                        <FormControl fullWidth>
                            <TextField
                                autoComplete="off"
                                type="text"
                                value={name}
                                placeholder="Coineus"
                                label="Name"
                                onChange={(ev) => {
                                    setName(ev.target.value)
                                }}
                            />
                        </FormControl>
                    </Grid>

                    <Grid item xs={12}>
                        <FormControl fullWidth>
                            <TextField
                                autoComplete="off"
                                type="text"
                                value={symbol}
                                placeholder="CEUS"
                                label="Symbol"
                                onChange={(ev) => {
                                    setSymbol(ev.target.value)
                                }}
                            />
                        </FormControl>
                    </Grid>

                    <Grid item xs={12}>
                        <FormControl fullWidth>
                            <TextField
                                autoComplete="off"
                                type="text"
                                value={totalSupply}
                                placeholder="2000000"
                                label="Total Supply"
                                onChange={(ev) => {
                                    setTotalSupply(ev.target.value)
                                }}
                            />
                        </FormControl>
                    </Grid>

                    <Grid item xs={12}>
                        <FormControl fullWidth>
                            <TextField
                                autoComplete="off"
                                type="text"
                                value={taxAmount}
                                placeholder="0"
                                label="Tax Amount"
                                onChange={(ev) => {
                                    setTaxAmount(ev.target.value)
                                }}
                            />
                        </FormControl>
                    </Grid>

                    <Grid item xs={12}>
                        <NetworkButton network={CHAIN_IDS.FUSE}>
                            <Button
                                variant="contained"
                                fullWidth
                                style={{marginTop: 10}}
                                onClick={launch}
                                disabled={!wallet.address || name === '' || symbol === '' || totalSupply === '' || taxAmount === ''}
                            >Launch</Button>
                            <Typography variant="caption">Fee: {launchFee} FUSE</Typography>
                        </NetworkButton>
                    </Grid>

                </Grid>
            </Paper>
        </Grid>

        <Grid item xs={12}>
            <Paper style={{padding: 20}}>
                <Typography style={{fontWeight: 700}}>My Tokens</Typography>
                <List disablePadding>
                    {
                        tokensByDeployer.map(t => {
                            return <ListItem key={t} disableGutters>
                                <ListItemText primary={t} primaryTypographyProps={{style: {fontSize: 12}}}/>
                                <ListItemSecondaryAction>
                                    <IconButton component={RouterLink} to={`/launchpad/fuse/${t}`}><SettingsIcon/></IconButton>
                                </ListItemSecondaryAction>
                            </ListItem>
                        })
                    }
                </List>
            </Paper>
        </Grid>


        {
            created !== null && <Grid item xs={12}>
                <Paper style={{padding: 20}}>
                    <Typography variant="h6">Token Created</Typography>
                    <Typography variant="caption">Contract Address: <Link href={`https://explorer.fuse.io/address/${created.contract_address}`} target="_blank">{created.contract_address}</Link></Typography>
                    <br/><br/>

                    <Button fullWidth variant="contained" component={Link} href={`https://app.voltage.finance/#/add/0x4e69ae0cd024754655b4ef74f24a8dcb39ba07e8/${created.contract_address}`} target="_blank">Add Liquidity</Button>

                </Paper>
            </Grid>
        }

        <CoineusLoading open={pending} label="Transaction Pending"/>
    </Grid>
}