import {useParams} from "react-router-dom";
import launchpad_token_abi from "../abis/Coineus/Launchpad/Token.json";
import launchpad_taxcollector_abi from "../abis/Coineus/Launchpad/TaxCollector.json";
import Web3 from "web3";
import {useSelector} from "react-redux";
import {makeBatchRequest} from "../utils/promisify";
import {useEffect, useState} from "react";
import {
    Avatar,
    Button,
    Dialog, DialogActions, DialogContent, FormControl,
    Grid,
    List,
    ListItem,
    ListItemAvatar,
    ListItemText,
    Paper, TextField,
    Typography
} from "@mui/material";
import {CoineusCryptoFormat} from "../utils/currency_format";
import {NetworkButton} from "../components/Coineus";
import {CHAIN_IDS} from "../constants";
import CoineusLoading from "../components/CoineusLoading";

export default function LaunchpadToken() {

    const {network, contract_address} = useParams();
    const {wallet, rpc} = useSelector(state => state.coineus);

    const [pending, setPending] = useState(false);
    const [tokenDetails, setTokenDetails] = useState({});

    const getTokenInfo = () => {

        const web3 = new Web3(rpc[network]);
        const token = new web3.eth.Contract(launchpad_token_abi, contract_address);

        const calls = [
            token.methods.name().call,
            token.methods.symbol().call,
            token.methods.decimals().call,
            token.methods.totalSupply().call,
            token.methods.owner().call,
            token.methods.TAX_COLLECTOR().call,
            token.methods.taxAmount().call
        ];

        makeBatchRequest(web3, calls).then(resp => {

            const [
                name,
                symbol,
                decimals,
                totalSupply,
                owner,
                taxCollector,
                taxAmount
            ] = resp;

            const tax_collector = new web3.eth.Contract(launchpad_taxcollector_abi, taxCollector);

            const tax_calls = [
                tax_collector.methods.taxesLength().call,
                web3.eth.getBalance(taxCollector)
            ];

            makeBatchRequest(web3, tax_calls).then(resp => {
                const [
                    taxesLength,
                    pendingTax
                ] = resp;

                if(parseInt(taxesLength) > 0) {
                    // get tax info

                    const taxInfoCalls = [];
                    for(let i = 0; i < parseInt(taxesLength); i++){
                        taxInfoCalls.push(
                            tax_collector.methods.taxes(i).call
                        )
                    }

                    makeBatchRequest(web3, taxInfoCalls).then(resp => {
                        setTokenDetails({
                            name,
                            symbol,
                            decimals,
                            totalSupply: totalSupply/(10**decimals),
                            owner,
                            taxCollector,
                            taxAmount,
                            taxesLength,
                            taxesDetails: resp,
                            pendingTax
                        });
                    });
                } else {

                    setTokenDetails({
                        name,
                        symbol,
                        decimals,
                        totalSupply: totalSupply/(10**decimals),
                        owner,
                        taxCollector,
                        taxAmount,
                        taxesLength,
                        pendingTax
                    });

                }

            })
        })

    }

    useEffect(getTokenInfo, []);

    const addTax = (amount, receiver, label, callback) => {

        const web3 = new Web3(window.provider);
        const tax_collector = new web3.eth.Contract(launchpad_taxcollector_abi, tokenDetails.taxCollector);

        tax_collector.methods.addTax(amount, receiver, label)
            .send({from: wallet.address})
            .once('transactionHash', function (tx) {
                setPending(true)
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                callback();
                getTokenInfo();
                setPending(false);
            });

    }

    const distributeTaxes = () => {

        const web3 = new Web3(window.provider);
        const tax_collector = new web3.eth.Contract(launchpad_taxcollector_abi, tokenDetails.taxCollector);

        tax_collector.methods.distributeTaxes()
            .send({from: wallet.address})
            .once('transactionHash', function (tx) {
                setPending(true)
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                getTokenInfo();
                setPending(false);
            });

    }

    return <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
        <Grid item xs={12}>
            <Paper style={{padding: 20}}>
                <List disablePadding>
                    <ListItem disableGutters disablePadding>
                        <ListItemAvatar>
                            <Avatar src={`https://coineus.app/assets/tokens/${network}/${contract_address}/logo.png`}/>
                        </ListItemAvatar>
                        <ListItemText
                            primary={tokenDetails.name}
                            secondary={tokenDetails.symbol}
                        />
                        <ListItemText
                            primary={CoineusCryptoFormat(tokenDetails.totalSupply)}
                            secondary={`${tokenDetails.decimals} Decimals`}
                        />
                    </ListItem>
                </List>

            </Paper>
        </Grid>


        <Grid item xs={12}>
            <Paper style={{padding: 20}}>
                <Typography>Taxes</Typography>
                <List disablePadding>

                    <ListItem disableGutters disablePadding>
                        <ListItemText
                            primary={tokenDetails.taxCollector}
                            primaryTypographyProps={{variant: 'caption'}}
                            secondary="Tax Collector"
                        />
                    </ListItem>


                    {
                        tokenDetails.taxesLength > 0 ?

                            <TaxesDetails data={tokenDetails.taxesDetails} /> :

                            <Typography>No Taxes Setup </Typography>
                    }

                </List>

                <AddTaxDialog network={network.toUpperCase()} submit={addTax}/>

            </Paper>
        </Grid>
        <Grid item xs={12}>
            <Paper style={{padding: 20}}>
                <List disablePadding>
                    <ListItem disableGutters>
                        <ListItemText primary={`${tokenDetails.pendingTax / (10**18)} FUSE`} secondary="Pending Tax Balance" />
                    </ListItem>
                </List>
                <Button variant="contained" fullWidth onClick={distributeTaxes}>Distribute</Button>
            </Paper>
        </Grid>
        <CoineusLoading open={pending} label="Transaction Pending"/>
    </Grid>
}

function TaxesDetails(props) {
    const {data} = props;

    const [totalTax, setTotalTax] = useState(0);

    useEffect(() => {
        let total = 0;
            data.map(d => {
                total += parseInt(d.amount);
            })
        setTotalTax(total);
    }, [data])

    return <>
        {
            totalTax !== 1000 && <Grid item xs={12}>
                <Paper style={{padding: 20, border: '1px solid #fbca00', marginTop: 10, marginBottom: 10}}>
                    <Typography color="primary" fontWeight={700}>Notice</Typography>
                    <Typography>Total tax amounts need to sum up to 1000.</Typography>
                </Paper>
            </Grid>
        }
        {
            data.map(d => {
                return <ListItem disableGutters disablePadding>
                    <ListItemText
                        primary={d.label}
                        secondary={"0x.." + d.receiver.substr(-8)}
                    />
                    <ListItemText
                        primary={d.amount/10 + "%"}
                        primaryTypographyProps={{textAlign: 'right'}}
                    />
                </ListItem>
            })
        }
    </>
}

function AddTaxDialog(props) {

    const {network, submit} = props;

    const [open, setOpen] = useState(false);
    const [taxAmount, setTaxAmount] = useState('');
    const [taxReceiver, setTaxReceiver] = useState('');
    const [taxLabel, setTaxLabel] = useState('');

    return <>
        <NetworkButton network={CHAIN_IDS[network]}>
            <Button
                fullWidth
                variant="contained"
                style={{marginTop: 10}}
                onClick={()=>setOpen(true)}
            >ADD TAX</Button>
        </NetworkButton>

        <Dialog open={open} onClose={()=>setOpen(false)}>
            <DialogContent>
                <Grid container spacing={2}>

                    <Grid item xs={12}>
                        <FormControl fullWidth>
                            <TextField
                                autoComplete="off"
                                type="text"
                                value={taxAmount}
                                placeholder="20"
                                label="Tax Share"
                                onChange={(ev) => {
                                    setTaxAmount(ev.target.value)
                                }}
                                helperText="This number divided by 1000 equals the share of taxes sent to receiver. Ex: 20/1000 = 2% of taxes."
                            />
                        </FormControl>
                    </Grid>

                    <Grid item xs={12}>
                        <FormControl fullWidth>
                            <TextField
                                autoComplete="off"
                                type="text"
                                value={taxReceiver}
                                placeholder="0x0"
                                label="Tax Receiver"
                                onChange={(ev) => {
                                    setTaxReceiver(ev.target.value)
                                }}
                                helperText="Address to send the taxes to."
                            />
                        </FormControl>
                    </Grid>

                    <Grid item xs={12}>
                        <FormControl fullWidth>
                            <TextField
                                autoComplete="off"
                                type="text"
                                value={taxLabel}
                                placeholder="Marketing"
                                label="Tax Label"
                                onChange={(ev) => {
                                    setTaxLabel(ev.target.value)
                                }}
                                helperText="Description of what the tax is used for."
                            />
                        </FormControl>
                    </Grid>
                </Grid>
            </DialogContent>
            <DialogActions>
                <Button onClick={()=>{
                    setOpen(false)
                }}>Cancel</Button>
                <Button variant="contained" onClick={()=>{
                    submit(taxAmount, taxReceiver, taxLabel, ()=>setOpen(false))
                }}>ADD</Button>
            </DialogActions>
        </Dialog>
    </>
}