import {Link as RouterLink} from "react-router-dom";
import {
    Avatar,
    AvatarGroup,
    Button,
    Grid,
    List,
    ListItem,
    ListItemAvatar,
    ListItemText,
    Paper,
    Skeleton
} from "@mui/material";
import {CoineusCryptoFormat} from "../utils/currency_format";
import useFarmInfo from "../hooks/useFarmInfo";
import useSingleTokenFarmInfo from "../hooks/useSingleTokenFarmInfo";

export default function Farm() {

    return <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>

        <FarmSummary
            contract_address="0xC2B83a3C3199Fb29Ce1d8Dfc681001AF3e97e004"
            network="fuse"
        />

        <FarmSummary
            contract_address="0x7243265f91e138a11548DF4253E3ec056b6348f8"
            network="fuse"
        />

        <FarmSummary
            contract_address="0xbbd77F9475de999628A6C0f5b31589585fBbc119"
            network="fuse"
        />

        <SingleTokenFarmSummary
            contract_address="0x225b81A6aCaF6448d74e30190375b12970D5Fa30"
            network="fuse"
        />

    </Grid>
}

function FarmSummary(props) {

    const {contract_address, network} = props;

    const {farmInfo, loading} = useFarmInfo(contract_address, network);

    if(loading){
        return <Grid item xs={12}>
            <Paper style={{padding:20}}>
                <Skeleton />
                <Skeleton />
            </Paper>
        </Grid>
    }

    return <Grid item xs={12}>
        <Paper>
            <List>
                <ListItem>
                    <ListItemAvatar style={{marginRight: 10}}>
                        <AvatarGroup>
                            <Avatar
                                src={`https://coineus.app/assets/tokens/${network}/${farmInfo.token0}/logo.png`}/>
                            <Avatar
                                src={`https://coineus.app/assets/tokens/${network}/${farmInfo.token1}/logo.png`}/>
                        </AvatarGroup>
                    </ListItemAvatar>
                    <ListItemText
                        primary={CoineusCryptoFormat(farmInfo.totalStaked)}
                        secondary="Total Staked"
                    />
                    <ListItemText
                        primary={farmInfo.apy.toFixed(2) + "%"}
                        secondary="Est. APY"
                        primaryTypographyProps={{textAlign:'right'}}
                        secondaryTypographyProps={{textAlign:'right'}}
                    />
                </ListItem>

                <ListItem>
                    <Button
                        variant="contained"
                        fullWidth
                        component={RouterLink} to={`/farm/${network}/${contract_address}`}
                    >VIEW</Button>
                </ListItem>

            </List>
        </Paper>
    </Grid>
}

function SingleTokenFarmSummary(props) {

    const {contract_address, network} = props;

    const {farmInfo, loading} = useSingleTokenFarmInfo(contract_address, network);

    if(loading){
        return <Grid item xs={12}>
            <Paper style={{padding:20}}>
                <Skeleton />
                <Skeleton />
            </Paper>
        </Grid>
    }

    return <Grid item xs={12}>
        <Paper>
            <List>
                <ListItem>
                    <ListItemAvatar style={{marginRight: 10}}>
                        <Avatar src={`https://coineus.app/assets/tokens/${network}/${farmInfo.token0}/logo.png`}/>
                    </ListItemAvatar>
                    <ListItemText
                        primary={CoineusCryptoFormat(farmInfo.totalStaked)}
                        secondary="Total Staked"
                    />
                    <ListItemText
                        primary={farmInfo.apy.toFixed(2) + "%"}
                        secondary="Est. APY"
                        primaryTypographyProps={{textAlign:'right'}}
                        secondaryTypographyProps={{textAlign:'right'}}
                    />
                </ListItem>

                <ListItem>
                    <Button
                        variant="contained"
                        fullWidth
                        component={RouterLink} to={`/earn/${network}/${contract_address}`}
                    >VIEW</Button>
                </ListItem>

            </List>
        </Paper>
    </Grid>
}